<template>
  <div class="filter-content">
    <div class="filter-group">
      <el-divider>类别</el-divider>

      <el-checkbox-group v-model="typeSelected">
        <div class="filter-item" v-for="item of types" :key="item">
          <el-checkbox :label="item" color="white"></el-checkbox>
        </div>
      </el-checkbox-group>
    </div>

    <div class="filter-group">
      <el-divider>级别</el-divider>

      <el-checkbox-group v-model="levelSelected">
        <div class="filter-item" v-for="item of levels" :key="item">
          <el-checkbox :label="item"></el-checkbox>
        </div>
      </el-checkbox-group>
    </div>
  </div>
</template>

<script>
export default {
  computed: {
    project() {
      return this.$store.state.project;
    }
  },
  data() {
    return {
      visible: false,
      types: ["自然保护区", "森林公园", "风景名胜区", "地质公园", "湿地公园"],
      levels: ["国家级", "省级", "县级"],
      typeSelected: [],
      levelSelected: []
    };
  },
  mounted() {
    this.initType();
  },
  watch: {
    typeSelected() {
      this.filter();
    },
    levelSelected() {
      this.filter();
    }
  },
  methods: {
    initType() {
      this.typeSelected = this.types;
      this.levelSelected = this.levels;
    },
    filter() {
      this.project &&
        this.project.features.forEach(f => {
          f.active =
            this.typeSelected.includes(f.properties["类型"]) &&
            this.levelSelected.includes(f.properties["级别"]);
        });

      this.$app.$emit("refresh");
    }
  }
};
</script>
