<template>
  <div class="filter-content">
    <div class="filter-title">美丽河湖 （{{project.features.length}} 个）</div>

    <div class="filter-tree">
      <el-tree :data="items" @node-click="nodeSelected"></el-tree>
    </div>
  </div>
</template>


<script>
export default {
  computed: {
    project() {
      return this.$store.state.project;
    }
  },
  data() {
    return {
      visible: false,
      items: [
        { label: "上城区", children: [] },
        { label: "下城区", children: [] },
        { label: "西湖区", children: [] },
        { label: "江干区", children: [] },
        { label: "拱墅区", children: [] },
        { label: "滨江区", children: [] },
        { label: "萧山区", children: [] },
        { label: "余杭区", children: [] },
        { label: "临安区", children: [] },
        { label: "富阳区", children: [] },
        { label: "建德市", children: [] },
        { label: "桐庐县", children: [] },
        { label: "淳安县", children: [] }
      ]
    };
  },
  mounted() {
    this.initData();
  },
  watch: {
    typeSelected() {
      this.filter();
    },
    levelSelected() {
      this.filter();
    }
  },
  methods: {
    initData() {
      const features = this.project.features;
      this.items.forEach(item => {
        const name = item.label;

        features.forEach(feature => {
          if (feature.properties["所在县市区"].indexOf(name) >= 0) {
            feature.label =
              feature.properties["点注记"] ||
              feature.properties["线注记"] ||
              feature.properties["面注记"];
            item.children.push(feature);
          }
        });
      });
    },
    nodeSelected(node) {
      if (!node.children) {
        this.$store.commit("feature_changed", node);
      }
    }
  }
};
</script>
